}
input{
    margin-top: 10px;
    display:block;
    width:100%;
    border:none;
    border-bottom:1.5px solid #757575;
    background-color: transparent;
    opacity: 0.5;
    
}
.bar 	{ position:relative; display:block; width:100%; }
.bar:before, .bar:after 	{
  content:'';
  height:2px; 
  width:0;
  top: 0;
  position:absolute;
  background:#5264AE; 
  transition:0.2s ease all; 
  -moz-transition:0.2s ease all; 
  -webkit-transition:0.2s ease all;
}
.bar:before {
  left:50%;
}
.bar:after {
  right:50%; 
}

input:focus ~ .bar:before, input:focus ~ .bar:after {
  width:50%;
}


#tableContainer{
     width:100%;
     height:auto;
     display: grid;
    background-color: antiquewhite;
    grid-template-columns: 90px 90px 90px 90px 90px 90px 90px 90px 90px 90px 90px 90px 90px 90px ;
    
 }


.Datatable {
  cursor: pointer;
  transition: color 0.15s ease-in-out;
  font-size: 80%;
  width: 100%;
  word-wrap: break-word;
  white-space: normal !important;
  overflow-x: hidden; 
  table-layout: fixed;
}
